









import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';
import eventBus from '@/components/EventBus';

@Component
export default class Amount extends Vue {
  @Prop() readonly currentType!: string;
  content = '0';

  mounted() {
    eventBus.$on('amountChange', (val: string) => {
      this.content = val;
    });
  }
}
